radio_bgWhite,\n#axp_post_radio_bgWhite~label,\n#axp_post_radio_bgTransparent,\n#axp_post_radio_bgTransparent~label {\n    cursor: pointer;\n}\n\n\n/*  */\n.axpc_post_line {\n    grid-area: post_line;\n    background-image: linear-gradient(0deg, transparent, #aaa, transparent);\n}\n\n/*  */\n.axpc_post_thumbnailClick {\n    text-align: center;\n}\n\n/* post form */\n#axp_post_div_input {\n    grid-area: post_div_input;\n}\n\n.axpc_post_input {\n    display: grid;\n    align-items: center;\n    grid-template-columns: max-content max-content 1fr;\n    grid-template-areas:\n        \"name_property      name_required      .\"\n        \"text_name          text_name          text_name\"\n        \"title_property     title_required     .\"\n        \"text_title         text_title         text_title\"\n        \"message_property   message_required   .\"\n        \"textarea_message   textarea_message   textarea_message\"\n        \"watchList          watchList          watchList\";\n}\n\n.axpc_post_name_property,\n.axpc_post_title_property,\n.axpc_post_message_property {\n    line-height: normal;\n    background: #ccc;\n    padding: 2px 10px 5px;\n    border: #ddd 2px solid;\n    border-bottom: none;\n    border-radius: 10px 10px 0 0;\n    /* border */\n    margin-left: -2px;\n}\n\n.axpc_post_name_required,\n.axpc_post_title_required,\n.axpc_post_message_required {\n    font-size: 0.7em;\n    font-weight: bold;\n    background-color: #ff4444;\n    color: #eee;\n    padding: 0.2em 3px;\n    border-radius: 5px;\n    width: fit-content;\n}\n\n#axp_post_text_name,\n#axp_post_text_title,\n#axp_post_textarea_message {\n    margin-bottom: 10px;\n    /*  */\n    margin-top: -3px;\n}\n\n#axp_post_text_name,\n#axp_post_text_title {\n    line-height: 1.5lh;\n    padding: 0 4px;\n}\n\n#axp_post_textarea_message {\n    field-sizing: content;\n    min-height: 4.5lh;\n    height: auto;\n    resize: vertical;\n    line-height: 1.3em;\n    padding: 5px;\n    font-size: 13px;\n}\n\n.axpc_post_name_property {\n    grid-area: name_property;\n}\n\n.axpc_post_name_required {\n    grid-area: name_required;\n}\n\n#axp_post_text_name {\n    grid-area: text_name;\n}\n\n.axpc_post_title_property {\n    grid-area: title_property;\n}\n\n.axpc_post_title_required {\n    grid-area: title_required;\n}\n\n#axp_post_text_title {\n    grid-area: text_title;\n}\n\n.axpc_post_message_property {\n    grid-area: message_property;\n}\n\n.axpc_post_message_required {\n    grid-area: message_required;\n}\n\n#axp_post_textarea_message {\n    grid-area: textarea_message;\n}\n\n.axpc_post_watchList {\n    grid-area: watchList;\n}\n\n/* status */\n.axpc_post_status {\n    grid-area: post_status;\n    display: grid;\n    grid-template-areas:\n        \"size_property    size_colon    size_value\"\n        \"bg_property      bg_colon      bg_value\"\n        \"based_property   based_colon   based_value\";\n    grid-template-columns: max-content max-content 1fr;\n    align-items: center;\n}\n\n[class^=\"axpc_post_status_\"][class$=\"_property\"] {\n    text-align: right;\n}\n\n.axpc_post_status_size_property {\n    grid-area: size_property;\n}\n\n.axpc_post_status_bg_property {\n    grid-area: bg_property;\n}\n\n.axpc_post_status_based_property {\n    grid-area: based_property;\n}\n\n.axpc_post_status_size_value {\n    grid-area: size_value;\n}\n\n.axpc_post_status_based_value {\n    grid-area: based_value;\n}\n\n[class^=\"axpc_post_status_\"][class$=\"_colon\"] {\n    width: 1.5em;\n    text-align: center;\n}\n\n.axpc_post_status_size_colon {\n    grid-area: size_colon;\n}\n\n.axpc_post_status_bg_colon {\n    grid-area: bg_colon;\n}\n\n.axpc_post_status_based_colon {\n    grid-area: based_colon;\n}\n\n/* notice */\n#axp_post_div_notice {\n    padding: 20px;\n    border-radius: 20px;\n    background-color: #dedede;\n}\n\n#axp_post_div_notice>u {\n    font-size: 120%;\n    font-weight: bold;\n}\n\n\n#axp_post_div_notice div {\n    text-indent: 1em;\n    margin: 5px auto;\n}\n\n/*  */\n#axp_post_div_button {\n    border-radius: 20px;\n    background-color: #dedede;\n    padding: 20px;\n}\n\n/*  */\n#axp_post_span_message {\n    color: #d00;\n    font-weight: 600;\n}\n\n/*  */\n#axp_post_button_upload {\n    width: 260px;\n    display: block;\n    margin: auto;\n    transition: 0.5s ease-in-out;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n@keyframes spin {\n    0% {\n        transform: rotate(0deg);\n    }\n\n    100% {\n        transform: rotate(360deg);\n    }\n}\n\n/*  */\n#axp_post_button_upload_loading {\n    width: 24px;\n    height: 24px;\n    border-radius: 50%;\n    border: 0.25rem solid rgba(255, 255, 255, 0.2);\n    border-top-color: rgb(255, 255, 255);\n    animation: spin 1s infinite linear;\n}\n\n/*  */\n.axpc_post_overlay {\n    position: fixed;\n    background: #0008;\n    visibility: hidden;\n    opacity: 0;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.axpc_post_overlay,\n.axpc_post_overlay * {\n    cursor: zoom-out;\n}\n\n.axpc_post_thumbnail {\n    cursor: zoom-in;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 120px;\n    height: 120px;\n    margin: auto;\n    background-color: #fff;\n}\n\n.axpc_post_thumbnail>img {\n    background-color: #fff;\n}\n\n#axp_post_checkbox_popup {\n    display: none;\n}\n\n#axp_post_checkbox_popup:checked+.axpc_post_overlay {\n    visibility: visible;\n    opacity: 1;\n    z-index: 10;\n    background-color: #0008;\n    width: 100%;\n}\n\n.axpc_post_overlayWindow {\n    border-radius: 15px;\n    min-width: fit-content;\n    max-width: 100%;\n    max-height: 100%;\n    position: fixed;\n    z-index: 11;\n    margin: 0 auto;\n    padding: 15px;\n    background: #fff5;\n    line-height: 0;\n}\n\n.axpc_post_overlayCloseButton {\n    cursor: pointer;\n    position: absolute;\n    box-sizing: border-box;\n    width: 40px;\n    height: 40px;\n    top: -35px;\n    right: -35px;\n    border: #666 solid 5px;\n    border-radius: 50%;\n    text-decoration: none;\n    color: #000;\n    background: #fff;\n    padding: 0;\n}\n\n.axpc_post_overlayCloseButton::after,\n.axpc_post_overlayCloseButton::before {\n    background-color: #666;\n    border-radius: 15% 15% 15% 15% / 50% 50% 50% 50%;\n    content: \"\";\n    display: block;\n    height: 22%;\n    left: 10%;\n    position: absolute;\n    top: 38%;\n    width: 80%;\n}\n\n.axpc_post_overlayCloseButton::before {\n    transform: rotate(135deg);\n}\n\n.axpc_post_overlayCloseButton::after {\n    transform: rotate(45deg);\n}\n\n.axpc_post_overlayCanvas {\n    max-width: calc(100vw - 120px);\n    max-height: calc(95vh - 110px);\n    object-fit: contain;\n    width: 100%;\n    height: 100%;\n}\n\n/* 599px */\n@media (max-width: 599px) {\n    .axpc_post_frame {\n        display: flex;\n        flex-direction: column;\n    }\n\n    /*  */\n    .axpc_post_line {\n        height: 2px;\n        margin: 20px 10px;\n        background: linear-gradient(90deg, transparent, #aaa, transparent)\n    }\n}"],"sourceRoot":""}]);
