 1);\n  }\n\n  #axp_main_checkbox_hamburger:checked~div>div {\n    border: 1px solid #333;\n    height: 2.5em;\n    padding: 0.5em;\n    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);\n  }\n\n  #axp_main_div_tab_menu>div {\n    display: flex;\n    justify-content: center;\n    margin: 0;\n    padding: 0.5em 0;\n    width: 100%;\n    border-radius: 10px 10px 10px 10px;\n  }\n}\n\n/*  */\n#axp_main_div_headerText {\n  padding: 4px 10px 0;\n  color: #fff;\n  overflow: hidden;\n  white-space: pre;\n  flex-grow: 1;\n  cursor: pointer;\n  text-overflow: ellipsis;\n}\n\n/*  */\n#axp_main_div_headerText:active {\n  overflow: visible;\n  white-space: normal;\n}\n\n/*  */\n#axp_main_div_tabContent>article {\n  display: none;\n  flex-grow: 1;\n  flex-direction: column;\n  overflow: hidden;\n}\n\n/* input */\n#axp_main_div_tabContent input {\n  font-size: 13px;\n}\n\n/*  */\n#axp_canvas {\n  width: 100%;\n  overflow: hidden;\n  position: relative;\n  flex-grow: 1;\n}\n\n/*  */\n#axp_canvas_div_grayBackground {\n  width: 100%;\n  height: 100%;\n  background-color: #777;\n}\n\n/*  */\n#axp_canvas_canvas_main {\n  position: absolute;\n  image-rendering: pixelated;\n  z-index: 100;\n}\n\n/*  */\n#axp_canvas_div_grid {\n  /*  */\n  pointer-events: none;\n  image-rendering: auto;\n  position: absolute;\n  z-index: 600;\n}\n\n/*  */\n#axp_canvas_div_pointerPosition {\n  position: absolute;\n  right: 0;\n  bottom: 0;\n  font-family: monospace;\n  font-size: 18px;\n  font-weight: 500;\n  color: #fff;\n  background: rgba(0, 0, 0, 0.7);\n  box-shadow: #000;\n  z-index: 800;\n}\n\n/*  */\n#axp_canvas_div_debugInfo {\n  position: absolute;\n  top: 0;\n  left: 0;\n  font-family: monospace;\n  font-size: 1em;\n  background: rgba(0, 0, 0, 0.7);\n  box-shadow: #000;\n  z-index: 900;\n  height: 50%;\n  max-height: 95%;\n  resize: both;\n  overflow: hidden;\n}\n\n#axp_canvas_div_debugInfo>div:nth-of-type(1) {\n  font-family: monospace;\n  color: #fff;\n  font-size: 1em;\n  height: 24px;